
body {
  background-color: #EEEEEE;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.intro {
  padding-top: 20px;
}

.search-box {
  display: block;
  width: 50%;
  height: 45px;
  margin: 100px auto 10px auto;
  border: none;
  outline: 0;
  -webkit-box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  background-color: #FFFFFF;
  padding-left: 20px;
  font-size: 13px;
}
.query {
  display: block;
  outline: 0;
  width: 80%;
  float: left;
  height: 42px;
  border: none;
  font-size: 13px;
}
.lang {
  display: block;
  outline: 0;
  width: 10%;
  float: left;
  height: 42px;
  border: none;
  font-size: 13px;
}


.splitpane {
  width: 100%;
}
.heading {
  position: relative;
  height: 8px;
  padding-bottom: 20px;
}
.components-container {
  position: relative;
  height: calc(100vh - 62px);
  /* bottom: 0px; */
}
.splitter-pane-resizer.vertical {
  width: 30px;
  background-color: #0000004d;
  /* box-shadow: 0 3px 16px 0 rgba(0, 0, 0, 1); */
}

.search-results {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  height: calc(100vh - 62px);
  overflow-y: auto;
  overflow-x: hidden;
}

.iframe-wrapper {
  opacity: .62;
}
.iframe-wrapper:hover {
  opacity: 1;
}
.iframe {
  display: inline-block;
  border-radius: 20px;
  /* border: 1px solid #c3c3c3; */
  height: 400px;
  -webkit-transition: -webkit-box-shadow 0.2s ease-in-out;
  transition: -webkit-box-shadow 0.2s ease-in-out;
  transition: box-shadow 0.2s ease-in-out;
  transition: box-shadow 0.2s ease-in-out, -webkit-box-shadow 0.2s ease-in-out;
  border: none;
  background-color: white !important;
  -webkit-box-shadow: 0 3px 16px 0 rgba(0, 0, 0, .11);
          box-shadow: 0 3px 16px 0 rgba(0, 0, 0, .11);
}
.iframe:hover {
  border: 1px solid #FCFFB5;
}
.iframe-placeholder {
   background: url('data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg" enable-background="new 0 0 100% 100%" fill="#000000" width="100%" height="100%" viewBox="0 0 100% 100%"><text fill="%23C9C9C9" x="50%" y="50%" font-family="\'Lucida Grande\', sans-serif" font-size="24" text-anchor="middle">Loading</text></svg>') 0px 0px no-repeat;
}
.result {
    margin-bottom: 30px;
}
.show-hide {
  float: right;
  outline: 0;
  border: none;
  background: transparent;
}
.source {
  float: right;
  outline: 0;
  border: none;
  background: transparent;
  text-decoration: none;
  color: black;
  font-size: 12px;
}
.top {
    width: 90%;
    display: inline-block;
    padding: auto;
    margin: auto;
    padding-bottom: 5px;
}

.editor-wrapper {
  /* display: flex; */
  /* position: relative; */
  /* min-height: 500px; */
  /* height: 450px; */
  border: none;
  margin: 30px;
  margin-top: 0px;
    height: calc(100vh - 162px);
  /* height: calc(100vh - 62px); */
}
.quill-container {
  border:none !important;
  /* height: calc(100vh - 62px); */
}
.ql-toolbar.ql-snow {
  border:none !important;
  /* max-height: 80px; */
  /* overflow-y: auto; */
}
.ql-toolbar.ql-snow+.ql-container.ql-snow {
  border-top: 0 !important;
  border: none  !important;
}
.ql-editor {
  height: calc(100vh - 162px) !important;
  max-height: calc(100vh - 162px) !important;
  overflow-y: auto;
}
.editor {
  height: calc(100vh - 62px);
  /* min-height: 500px; */
  -webkit-box-shadow: 0 3px 16px 0 rgba(0, 0, 0, .09);
          box-shadow: 0 3px 16px 0 rgba(0, 0, 0, .09);
  border-radius: 8px;
  border: none !important;
  overflow: hidden;
  /* bottom: 0px; */
  background-color: white;
}
